.o-header1 {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
     
}

.o-access-container {
    width: 15rem;
    display: flex;
    justify-content: space-between;
    margin-right: 4rem;
    
   
    
}

.o-button{
    font-size: 25px;
    font-family: 'Raleway', sans-serif;
    -webkit-transition-duration: 0.4s; /* Safari */
    transition-duration: 0.4s;
    cursor: pointer;
    background: none;
    border: none;
    color: white;
    
}

.o-button-signin:hover{
    border-radius: 5px;
    background-color: #53d4db;
}

.o-button-login:hover{
    background-color: #53d4db;
}


.o-learn{
    font-family: 'Raleway', sans-serif;
    margin-left: 5rem;
    font-size: 20px;
    color: #53d4db;
}

/*  Responsive */
/* -------------------------------------------------  */
@media only screen and (max-width: 600px) {

    .o-header1 {
        padding-top: 0.3rem;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
         
    }

    .o-access-container {
        width: 12rem;
        display: flex;
        justify-content: space-between;
        margin-right: 4rem;
        
    }

    .o-button{
        font-size: 0.7rem !important;
        font-family: 'Raleway', sans-serif;
        -webkit-transition-duration: 0.4s; /* Safari */
        transition-duration: 0.4s;
        cursor: pointer;
        
    }

    .o-learn{
        color: #216366;
        font-size: 0.7rem;
        margin-left: 5rem;
    }
}

