/*---------------------------Container padre------------------------------*/
.o-content {
    box-sizing: border-box;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-around;
}
/*--------Containedor delos contenedores CarSlots and CardPile-------------*/
.o-container-player, .o-container-answers {
    width: 30em;
    height: 100vh;
    /*background-color: purple;*/
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 5rem;
}
/*-------------------------------------------------------------------------*/
.o-img {
    width: 8em;
    height: 8em;
}
/*-------------Contenedor de las cards: Drag and Drop ---------------------*/
#cardSlots, #cardPile {
    width: 30em;
    height: 85rem !important;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    box-shadow: 0 16px 32px 0 rgba(0,0,0,0.2);
    margin-top: 42rem;
}
/*------------------------Atributo de las Cards----------------------------*/

#cardSlots div, #cardPile div {   
    margin: 1em;
    width: 10em;
    height: 12em;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    -moz-border-radius: 7px;
    -webkit-border-radius: 7px;
    border-radius: 7px;
   
}
/*---------------------------------Card Drag -------------------------------*/
#cardPile div {
    background: #fff;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
/*---------------------------------Cards Drop---------------------------------*/
#cardSlots div {
    border: 0.5px dashed rgb(128, 128, 128);
  
}
/*-------------------------------------------------------------------------------*/
.o-steps{
    margin: 0;
    padding: 0;
    color: rgb(128, 128, 128);
}
/*----------------------------------------------------------------------------------*/
#cardPile div.ui-draggable-dragging {
    -moz-box-shadow: 0 0 .5em rgba(0, 0, 0, .8);
    -webkit-box-shadow: 0 0 .5em rgba(0, 0, 0, .8);
    box-shadow: 0 0 .5em rgba(0, 0, 0, .8);
  }
   
  /* Individually coloured cards */
  #card1.correct, #card2.correct,  #card3.correct, #card4.correct,
  #card5.correct, #card6.correct, #card7.correct, #card8.correct,
  #card9.correct, #card10.correct,#card11.correct, #card12.correct    { background: rgb(51, 255, 0); }
 
  
   
   
  /* "You did it!" message */
  #successMessage {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
    background-color: #48b4b9;
    position: absolute;
    left: 0 !important;    
    top: 100rem !important;   
    height: 25em !important;
     width: 100vw !important;
    z-index: 1;
   /* background: #dfd;*/
   /* border: 2px solid #333;*/
   border: 0 !important;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
    border-radius: 10px;
    -moz-box-shadow: .3em .3em .5em rgba(0, 0, 0, .8);
    -webkit-box-shadow: .3em .3em .5em rgba(0, 0, 0, .8);
    box-shadow: .3em .3em .5em rgba(0, 0, 0, .8);
    padding: 20px;
  }


  .o-img-fondo{
    position: relative;
     width: 100%;
     height: 100vh;
     background-image: linear-gradient(
         rgba(0, 0, 0, 0.3),
         rgba(0, 0, 0, 0.3)
       ),url('../../../src/Img/background1.svg');  
       background-repeat: no-repeat;
       background-size: cover;
       
 }

 .o-inicio-barco{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    margin-top: 1rem;
    
}

.o-text{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    margin-top: -2rem;
    
    
}

.o-button-inicio{
    width: 22rem;
    height: 4rem;
    border: none;
    border-radius: 10px;
    background-color: #53d4db;
    color: black;
    cursor: pointer;
    font-size: 20px;
}

.o-button-inicio:hover{
    background-color: #48b4b9;
}

.o-title-inicio{
    font-size: 4rem;
    color: white;
    margin-right: 5%;
}

.o-title-inicio-barco{
    font-size: 4rem;
    color: white;
    margin-right: 4%;
    margin-left: -33rem;
}

.o-text-info{
    font-size: 1.5rem;
    color: white;
    text-align: justify;
}

.o-text-info-3{
    font-size: 1.5rem;
    color: white;
    text-align: justify;
}


.o-column{
    width: 50%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    margin-right: 5%;
    
}

.o-barcoPapel{
    width: 20rem;
}

.o-bold-barco{
    font-size: 25px;
    font-weight: bold;
    margin-left: -34rem;
}


.o-figura{
    margin-right: 5%;
}

.o-div-barcoPapel{  
    width: 25rem;
    height: 25rem;
    display: flex;
    justify-content: center;
    background-color: white;
    background-size: cover;
    border-radius: 50%;
}


.o-back{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 60% !important;
}

/* RESPONSIVE LONG DISPLAY */
@media only screen and (min-width: 1400px) {

    .o-inicio-barco{
   
        margin-top: 10rem;
        
    }

    
.o-div-barcoPapel{  
    width: 35rem;
    height: 35rem;
}

    .o-barcoPapel{
        width: 30rem;
    }

    .o-bold-barco{
        
        margin-left: -47rem;
    }

    .o-title-inicio-barco{
        
        margin-left: -47rem;
    }

    .o-text{

        margin-top: 0rem;
        

    }

    .o-container-player, .o-container-answers {
 
      
        margin-top: 0rem;
    }

    #cardSlots, #cardPile {
        height: 85rem !important;
        margin-top: 30rem;
    }

    .o-back{
        
        margin-top: 26% !important;
    }
    
}