.o-img-fondo{
   position: relative;
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(
        rgba(0, 0, 0, 0.3),
        rgba(0, 0, 0, 0.3)
      ),url('../../../Img/background1.svg');  
      background-repeat: no-repeat;
      background-size: cover;
      
}

