.o-inicio-c{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 2rem;
    
}
.o-info-algoritmos{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 1rem;
    
    
}

.o-info-programas{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 8rem;
    
    
}
.o-algortimos-seccion{
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
   
    
    
}

.o-programas-seccion{
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-top: 5rem;

}

.o-button-inicio{
    width: 22rem;
    height: 4rem;
    border: none;
    border-radius: 10px;
    background-color: #53d4db;
    color: black;
    cursor: pointer;
    font-size: 20px;
}

.o-button-inicio:hover{
    background-color: #48b4b9;
}

.o-title-inicio{
    font-size: 4rem;
    color: white;
    margin-right: 0rem;
}

.o-text{
    font-family: 'Raleway', sans-serif;
    text-align: justify;
    font-size: 20px;
    color:white;

}

.o-text2{
    font-family: 'Raleway', sans-serif;
    text-align: center;
    font-size: 20px;
    color:white;

}
.o-resaltar{
    color: #53d4db;
    font-weight: bold;
}

.o-cuadros{
    
    margin-left: 4rem;
}

.o-left{
    width: 35%;
}

.o-left2{
    width: 50%;

}


.o-right{
    margin-left: 5rem;
    width: 50%;
}


/* RESPONSIVE LONG DISPLAY */
@media only screen and (min-width: 1400px) {

    .o-inicio-c{

        margin-top: 10rem;
        
    }

    .o-info-algoritmos{
      
        padding-top: 8rem;
        
    }

    .o-algortimos-seccion{

        padding-top: 8rem;
    
    }
    
    .o-cuadros{
        margin-top: 5rem;
        margin-left: 8rem;
    }

    .o-title-inicio{
        margin-right: 5%;
    }

    .o-programas-seccion{
        padding-top: 8rem;
    
    }
  }