/*---------------------------Container padre------------------------------*/
.o-content {
    box-sizing: border-box;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-around;
}
/*--------Containedor delos contenedores CarSlots and CardPile-------------*/
.o-container-player, .o-container-answers {
    width: 30em;
    height: 100vh;
    /*background-color: purple;*/
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
/*-------------------------------------------------------------------------*/
.o-img {
    width: 8em;
    height: 8em;
}
/*-------------Contenedor de las cards: Drag and Drop ---------------------*/
#cardSlots, #cardPile {
    width: 30em;
    height: 43em;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
/*------------------------Atributo de las Cards----------------------------*/

#cardSlots div, #cardPile div {   
    margin: 1em;
    width: 10em;
    height: 12em;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    -moz-border-radius: 7px;
    -webkit-border-radius: 7px;
    border-radius: 7px;
   
}
/*---------------------------------Card Drag -------------------------------*/
#cardPile div {
    background: #fff;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
}
/*---------------------------------Cards Drop---------------------------------*/
#cardSlots div {
    border: 0.5px dashed rgb(128, 128, 128);
  
}
/*-------------------------------------------------------------------------------*/
.o-steps{
    margin: 0;
    padding: 0;
    color: rgb(128, 128, 128);
}
/*----------------------------------------------------------------------------------*/
#cardPile div.ui-draggable-dragging {
    -moz-box-shadow: 0 0 .5em rgba(0, 0, 0, .8);
    -webkit-box-shadow: 0 0 .5em rgba(0, 0, 0, .8);
    box-shadow: 0 0 .5em rgba(0, 0, 0, .8);
  }
   
  /* Individually coloured cards */
   
  #card1.correct, #card2.correct,  #card3.correct, #card4.correct { background: rgb(51, 255, 0); }
 
   
   
  /* "You did it!" message */
  #successMessage {
    position: absolute;
    left: 580px;
    top: 250px;
    width: 0;
    height: 0;
    z-index: 100;
    background: #dfd;
    border: 2px solid #333;
    -moz-border-radius: 10px;
    -webkit-border-radius: 10px;
    border-radius: 10px;
    -moz-box-shadow: .3em .3em .5em rgba(0, 0, 0, .8);
    -webkit-box-shadow: .3em .3em .5em rgba(0, 0, 0, .8);
    box-shadow: .3em .3em .5em rgba(0, 0, 0, .8);
    padding: 20px;
  }


  .o-img-fondo{
    position: relative;
     width: 100%;
     height: 100vh;
     background-image: linear-gradient(
         rgba(0, 0, 0, 0.3),
         rgba(0, 0, 0, 0.3)
       ),url('../../../src/Img/background1.svg');  
       background-repeat: no-repeat;
       background-size: cover;
       
 }

